import "./main.css";
import { storage_clear, storage_read, storage_write } from "@tomnicolosi/storage-wrap";
import  { content, data } from "./intro.md";
import marked from "marked";
//import * as StorageWrap from "@tomnicolosi/storage-wrap";
// const StorageWrap = require("@tomnicolosi/storage-wrap");
const STORAGE_KEY = "test_storage";

(function () {
    function handle_error (error) {
        const output_target = document.querySelector(".js-output");
        const error_text = document.createTextNode(`Error: ${error}`);
        output_target.classList.add("error");
        output_target.innerHTML = "";
        output_target.appendChild(error_text);
    };

    function setPageData () {
        const intro_container = document.querySelector(".js-intro");
        intro_container.innerHTML = marked(content);
    };

    function output_stored_value () {
        const output_target = document.querySelector(".js-output");
        const stored_value = storage_read(STORAGE_KEY);

        const output_text = (
            stored_value
            ? document.createTextNode(`Local storage contains "${stored_value}".`)
            : document.createTextNode("There is no stored value")
        );
        
        output_target.innerHTML = '';
        output_target.appendChild(output_text);
    };

    function handle_click (e) {
        const text_input = document.getElementById("test-string");
        const text_value = text_input.value;
        if (text_value) {
            try {
                storage_write(STORAGE_KEY, text_value);
                output_stored_value();
            } catch (error) {
                handle_error(error);
            };
            text_input.value = null;
            e.target.blur();
        };
    };

    function handle_clear_click (e) {
        try {
            storage_clear("localStorage");
            output_stored_value();
        } catch (error) {
            handle_error(error);
        };
        e.target.blur();
    };

    function init () {
        try {
            output_stored_value();
        } catch (error) {
            handle_error(error);
        };

        const text_input = document.querySelector(".js-store-data");
        text_input.addEventListener("click", handle_click);
        
        const clear_button = document.querySelector(".js-clear-data");
        clear_button.addEventListener("click", handle_clear_click);
        setPageData();
    };

    window.addEventListener('DOMContentLoaded', init);
}());